<template>
  <div class="com-container">
    <div class="com-chart" ref="seller_ref"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import themeUtils from '../utils/theme_utils'
export default {
  data () {
    return {
      echartInstance: null,
      allData: null,
      presentPage: 1,
      maxPage: 0,
      timerId: null
    }
  },
  computed: {
    color () {
      return themeUtils.getThemeStyle(this.theme).color
    },
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateChart()
    }
  },
  created () {
    this.$socket.registerCallBack('sellerData', this.getData)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'sellerData',
      chartName: 'seller',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    // 在界面加载完成后主动进行大小适配
    this.screenAdapt()
  },
  destroyed () {
    clearInterval(this.timerId)
    window.removeEventListener('resize', this.screenAdapt)
    this.$socket.unregisterCallBack('sellerData')
  },
  methods: {
    initChart () {
      // 初始化配置
      const initOption = {
        title: {
          text: '|   商家销售统计',
          top: '5%'
        },
        grid: {
          top: '20%',
          left: '3%',
          right: '6%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line',
            lineStyle: {
              color: '#2D3443'
            },
            z: 0
          }
        },
        series: [
          {
            type: 'bar',
            label: {
              show: true,
              position: 'right',
              color: this.color
            },
            itemStyle: {
              color: new this.$echarts.graphic.LinearGradient(0, 0, 1, 0, [
                {
                  offset: 0,
                  color: '#5052EE'
                },
                {
                  offset: 1,
                  color: '#AB6EE5'
                }
              ])
            }
          }
        ]
      }
      // 实例化echarts并设置主题
      this.echartInstance = this.$echarts.init(this.$refs.seller_ref, this.theme)
      this.echartInstance.setOption(initOption)
      this.echartInstance.on('mouseover', () => {
        clearInterval(this.timerId)
      })
      this.echartInstance.on('mouseout', () => {
        this.startInterval()
      })
    },
    // 获取服务器数据
    getData (res) {
      this.allData = res
      this.allData.sort((a, b) => {
        return b.value - a.value
      })
      this.maxPage = this.allData.length % 5 ? Math.ceil(this.allData / 5) : this.allData.length / 5
      this.updateChart()
      this.startInterval()
    },
    // 更新图表
    updateChart () {
      const start = (this.presentPage - 1) * 5
      const end = this.presentPage * 5
      const showData = this.allData.slice(start, end)
      const sellerValue = showData.map(item => {
        return item.value
      })
      const sellerName = showData.map(item => {
        return item.name
      })
      // 获得数据后配置
      const dataOption = {
        yAxis: {
          data: sellerName
        },
        series: [
          {
            data: sellerValue
          }
        ]
      }
      this.echartInstance.setOption(dataOption)
    },
    //  每隔3秒自动切换下一页数据
    startInterval () {
      if (this.timerId) {
        clearInterval(this.timerId)
      }
      this.timerId = setInterval(() => {
        this.presentPage++
        if (this.presentPage > this.maxPage) {
          this.presentPage = 1
        }
        this.updateChart()
      }, 3000)
    },
    // 屏幕适应
    screenAdapt () {
      const titleWidth = this.$refs.seller_ref.offsetWidth / 100 * 3.6
      const adaptOption = {
        title: {
          textStyle: {
            fontSize: titleWidth
          }
        },
        tooltip: {
          axisPointer: {
            lineStyle: {
              width: titleWidth
            }
          }
        },
        series: [
          {
            barWidth: titleWidth,
            itemStyle: {
              barBorderRadius: [0, titleWidth / 2, titleWidth / 2, 0]
            }
          }
        ]
      }
      this.echartInstance.setOption(adaptOption)
      this.echartInstance.resize()
    }
  }
}
</script>

