<template>
  <div class="com-container">
    <div class="com-chart" ref="stock_ref"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      echartsInstance: null,
      allData: null,
      titleFontSize: 0,
      start: 0,
      timerId: null
    }
  },
  computed: {
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartsInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateChart()
    }
  },
  methods: {
    initChart () {
      this.echartsInstance = this.$echarts.init(this.$refs.stock_ref, this.theme)
      const initOption = {
        title: {
          text: '|   库存和销量分析',
          top: '5%',
          left: '1%'
        }
      }
      this.echartsInstance.setOption(initOption)
    },
    getData (res) {
      this.allData = res
      this.updateChart()
    },
    updateChart () {
      const centerArr = [
        ['18%', '40%'],
        ['50%', '40%'],
        ['82%', '40%'],
        ['34%', '75%'],
        ['66%', '75%']
      ]
      const colorArr = [
        ['#4FF778', '#0BA82C'],
        ['#E5DD45', '#E8B11C'],
        ['#E8821C', '#E55445'],
        ['#5052EE', '#AB6EE5'],
        ['#23E5E5', '#2E72BF']
      ]
      const showArr = this.allData.slice(this.start * 5, (this.start + 1) * 5)
      const seriesArr = showArr.map((item, index) => {
        return {
          type: 'pie',
          name: item.name,
          center: centerArr[index],
          hoverAnimation: false,
          label: {
            position: 'center',
            color: colorArr[index][0]
          },
          labelLine: {
            show: false
          },
          emphasis: {
            label: {
              show: true
            }
          },
          radius: ['25%', '30%'],
          data: [
            {
              name: item.name + '\n\n' + item.sales,
              value: item.sales,
              itemStyle: {
                color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [
                  {
                    offset: 0,
                    color: colorArr[index][0]
                  },
                  {
                    offset: 1,
                    color: colorArr[index][1]
                  }
                ])
              }
            },
            {
              value: item.stock,
              itemStyle: {
                color: '#333843'
              }
            }
          ]
        }
      })
      const updateOption = {
        series: seriesArr
      }
      this.echartsInstance.setOption(updateOption)
    },
    screenAdapt () {
      const seriesArr = []
      this.titleFontSize = this.$refs.stock_ref.offsetWidth / 100 * 3.6
      for (var i = 0; i < 5; i++) {
        seriesArr.push(
          {
            type: 'pie',
            label: {
              fontSize: this.titleFontSize / 2
            }
          }
        )
      }
      const adaptOption = {
        title: {
          textStyle: {
            fontSize: this.titleFontSize
          }
        },
        series: seriesArr
      }
      this.echartsInstance.setOption(adaptOption)
      this.echartsInstance.resize()
    },
    changeShow () {
      if (this.timerId) {
        clearInterval(this.timerId)
      }
      this.timerId = setInterval(() => {
        this.start++
        if (this.start > 1) {
          this.start = 0
        }
        this.updateChart()
      }, 5000)
    }
  },
  created () {
    this.$socket.registerCallBack('stockData', this.getData)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'stockData',
      chartName: 'stock',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    this.screenAdapt()
    this.changeShow()
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapt)
    clearInterval(this.timerId)
    this.$socket.unregisterCallBack('stockData')
  }
}
</script>

