<template>
  <div class="com-container">
    <div class="com-chart" ref="map_ref" @dblclick="handleBackToChina"></div>
  </div>
</template>

<script>
import axios from 'axios'
import { getProvinceMapInfo } from '@/utils/map_utils'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      echartsInstance: null,
      allData: null,
      maps: {}
    }
  },
  computed: {
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartsInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateData()
    }
  },
  methods: {
    async initChart () {
      const initOption = {
        title: {
          text: '|   商家分布',
          top: '5%'
        },
        geo: {
          type: 'map',
          map: 'china',
          top: '5%',
          bottom: '5%',
          itemStyle: {
            areaColor: '#2E72BF',
            borderColor: '#333'
          }
        },
        legend: {
          left: '5%',
          bottom: '5%',
          orient: 'vertical'
        }
      }
      this.echartsInstance = this.$echarts.init(this.$refs.map_ref, this.theme)
      const res = await axios.get('http://localhost:8999/statics/map/china.json')
      this.$echarts.registerMap('china', res.data)
      this.echartsInstance.setOption(initOption)
      this.echartsInstance.on('click', (arg) => {
        this.$socket.sendData({
          action: 'getData',
          socketType: 'provinceData',
          chartName: 'map',
          value: arg.name
        })
      })
    },
    async getProvinceData (res) {
      const provinceInfo = getProvinceMapInfo(res.value)
      const provincePingyin = provinceInfo.key
      const changeMapOption = {
        geo: {
          map: provincePingyin
        }
      }
      if (!this.maps[provincePingyin]) {
        const res = await axios.get('http://localhost:8999' + provinceInfo.path)
        this.maps[provincePingyin] = 1
        this.$echarts.registerMap(provincePingyin, res.data)
      }
      this.echartsInstance.setOption(changeMapOption)
    },
    getData (res) {
      this.allData = res
      this.updateData()
    },
    updateData () {
      const series = this.allData.map(item => {
        return {
          type: 'effectScatter',
          coordinateSystem: 'geo',
          name: item.name,
          data: item.children,
          rippleEffect: {
            scale: 5,
            brushType: 'stroke'
          }
        }
      })
      const legend = this.allData.map(item => {
        return item.name
      })
      const updateOption = {
        series,
        legend: {
          data: legend
        }
      }
      this.echartsInstance.setOption(updateOption)
    },
    screenAdapt () {
      const titleFontSize = this.$refs.map_ref.offsetWidth / 100 * 3.6
      const adaptOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
          }
        },
        legend: {
          itemWidth: titleFontSize / 2,
          itemHeight: titleFontSize / 2,
          itemGap: titleFontSize / 2,
          textStyle: {
            fontSize: titleFontSize / 2
          }
        }
      }
      this.echartsInstance.setOption(adaptOption)
      this.echartsInstance.resize()
    },
    handleBackToChina () {
      this.$socket.sendData({
        action: 'backToChina',
        socketType: 'backToChina'
      })
    },
    backToChina () {
      const backOption = {
        geo: {
          map: 'china'
        }
      }
      this.echartsInstance.setOption(backOption)
    }
  },
  created () {
    this.$socket.registerCallBack('mapData', this.getData)
    this.$socket.registerCallBack('provinceData', this.getProvinceData)
    this.$socket.registerCallBack('backToChina', this.backToChina)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'mapData',
      chartName: 'map',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    this.screenAdapt()
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapt)
    this.$socket.unregisterCallBack('mapData')
    this.$socket.unregisterCallBack('provinceData')
    this.$socket.unregisterCallBack('backToChina')
  }
}
</script>

