<template>
  <div class="com-container">
    <div class="title-com" :style="titleStyle">
      <div class="title-wrap" @click="handleClick">
        <span class="title" :style="titleStyle">{{title}}</span>
        <span class="iconfont arrow" :style="titleStyle">&#xe6eb;</span>
      </div>
      <div :class="titleWrapClass" :style="titleListHeightC">
        <div v-for="item in selectedTitle" :key="item.key" @click="titleSelect(item)">{{item.text}}</div>
      </div>
    </div>
    <div class="com-chart" ref="trend_ref"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import themeUtils from '../utils/theme_utils'
export default {
  data () {
    return {
      echartsInstance: null,
      allData: null,
      titleWrapClass: ['title-list'],
      title: '',
      titleFontSize: '',
      titleListHeight: 0
    }
  },
  computed: {
    selectedTitle () {
      if (!this.allData) {
        return null
      } else {
        return this.allData.type.filter(item => {
          return item.text !== this.title
        })
      }
    },
    titleStyle () {
      return {
        fontSize: this.titleFontSize + 'px',
        color: themeUtils.getThemeStyle(this.theme).color
      }
    },
    titleListHeightC () {
      return {
        height: this.titleListHeight * 3 + 'px',
        color: themeUtils.getThemeStyle(this.theme).color,
        backgroundColor: themeUtils.getThemeStyle(this.theme).titleBackgroundColor
      }
    },
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartsInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateChart()
    }
  },
  created () {
    this.$socket.registerCallBack('trendData', this.getData)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'trendData',
      chartName: 'trend',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    this.screenAdapt()
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapt)
    this.$socket.unregisterCallBack('trendData')
  },
  methods: {
    initChart () {
      this.echartsInstance = this.$echarts.init(this.$refs.trend_ref, this.theme)
      const initOption = {
        grid: {
          top: '40%',
          left: '3%',
          right: '8%',
          bottom: '3%',
          containLabel: true
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          left: '5%',
          top: '20%',
          icon: 'circle'
        },
        xAxis: {
          name: '月份',
          type: 'category',
          boundaryGap: false
        },
        yAxis: {
          type: 'value'
        }
      }
      this.echartsInstance.setOption(initOption)
    },
    getData (res) {
      this.allData = res
      this.title = res.type[0].text
      this.updateChart()
    },
    updateChart (key) {
      if (key === undefined) {
        key = 'map'
      }
      const colorArr1 = [
        'rgba(11, 168, 44, 0.5)',
        'rgba(44, 110, 255, 0.5)',
        'rgba(22, 242, 217, 0.5)',
        'rgba(254, 33, 30, 0.5)',
        'rgba(250, 105, 44, 0.5)'
      ]
      const colorArr2 = [
        'rgba(11, 168, 44, 0)',
        'rgba(44, 110, 255, 0)',
        'rgba(22, 242, 217, 0)',
        'rgba(254, 33, 30, 0)',
        'rgba(250, 105, 44, 0)'
      ]
      const { data, unit } = this.allData[key]
      const seriesData = data.map((item, index) => {
        return {
          type: 'line',
          name: item.name,
          data: item.data,
          stack: 'map',
          areaStyle: {
            color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: colorArr1[index]
              },
              {
                offset: 1,
                color: colorArr2[index]
              }
            ])
          }
        }
      })
      const legendArr = data.map(item => {
        return item.name
      })
      const dataOption = {
        xAxis: {
          data: this.allData.common.month
        },
        yAxis: {
          name: '销量(' + unit + ')'
        },
        series: seriesData,
        legend: {
          data: legendArr
        }
      }
      this.echartsInstance.setOption(dataOption)
    },
    handleClick () {
      if (this.titleListHeight === 0) {
        this.titleListHeight = this.titleFontSize
      } else {
        this.titleListHeight = 0
      }
    },
    titleSelect (item) {
      this.title = item.text
      this.titleListHeight = 0
      this.updateChart(item.key)
    },
    screenAdapt () {
      if (this.titleListHeight !== 0) {
        this.titleListHeight = this.titleFontSize
      }
      this.titleFontSize = this.$refs.trend_ref.offsetWidth / 100 * 3.6
      const adaptOption = {
        legend: {
          itemWidth: this.titleFontSize / 2,
          itemHeight: this.titleFontSize / 2,
          textStyle: {
            fontsize: this.titleFontSize / 2
          }
        }
      }
      this.echartsInstance.setOption(adaptOption)
      this.echartsInstance.resize()
    }
  }
}
</script>

<style lang="less" scoped>
  .com-container{
    position: relative;
  }
  .title-com{
    position: absolute;
    z-index: 10;
    top: 5%;
    left: 1%;
  }
  .title-wrap{
    color: #fff;
    font-weight: bolder;
    .title{
      border-left: .2rem solid #fff;
      padding-left: 1rem;
    }
  }
  .title-list{
    color: #fff;
    height: 0;
    transition: height .2s ease-in-out;
    overflow: hidden;
  }
  .title-com{
    cursor: pointer;
    user-select: none;
  }
  .arrow{
    margin-left: 10px;
  }
</style>
