<template>
  <div class="com-container">
    <div class="com-chart" ref="rank_ref"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      echartInstance: null,
      allData: null,
      startValue: 0,
      endValue: 9,
      timerId: null
    }
  },
  computed: {
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateChart()
    }
  },
  methods: {
    initChart () {
      this.echartInstance = this.$echarts.init(this.$refs.rank_ref, this.theme)
      const initOption = {
        title: {
          text: '|   地区销售排行',
          left: '1%',
          top: '5%'
        },
        grid: {
          top: '40%',
          left: '5%',
          right: '5%',
          bottom: '5%',
          containLabel: true
        },
        tooltip: {},
        xAxis: {
          type: 'category'
        },
        yAxis: {
          type: 'value',
          name: '销售金额（万）'
        },
        series: [
          {
            type: 'bar'
          }
        ]
      }
      this.echartInstance.setOption(initOption)
    },
    getData (res) {
      this.allData = res
      this.startMove()
      this.updateChart()
    },
    updateChart () {
      this.allData = this.allData.sort((a, b) => {
        return b.value - a.value
      })
      const colorArr = [
        ['#0BA82C', '#4FF778'],
        ['#2E72BF', '#23E5E5'],
        ['#5052EE', '#AB6EE5']
      ]
      const xData = this.allData.map(item => {
        return item.name
      })
      const seriesData = this.allData.map(item => {
        return item.value
      })
      const updateOption = {
        xAxis: {
          data: xData
        },
        series: {
          data: seriesData,
          itemStyle: {
            color: arg => {
              const val = arg.data
              let itemColor = []
              if (val > 300) {
                itemColor = colorArr[0]
              } else if (val > 200) {
                itemColor = colorArr[1]
              } else {
                itemColor = colorArr[2]
              }
              return new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                {
                  offset: 0,
                  color: itemColor[0]
                },
                {
                  offset: 1,
                  color: itemColor[1]
                }
              ])
            }
          }
        },
        dataZoom: {
          show: false,
          startValue: this.startValue,
          endValue: this.endValue
        }
      }
      this.echartInstance.setOption(updateOption)
    },
    screenAdapt () {
      const titleFontSize = this.$refs.rank_ref.offsetWidth / 100 * 3.6
      const adaptOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
          }
        },
        series: [
          {
            barWidth: titleFontSize,
            itemStyle: {
              barBorderRadius: [titleFontSize / 2, titleFontSize / 2, 0, 0]
            }
          }
        ]
      }
      this.echartInstance.setOption(adaptOption)
      this.echartInstance.resize()
    },
    startMove () {
      if (this.timerId) {
        clearInterval(this.timerId)
      }
      this.timerId = setInterval(() => {
        this.startValue++
        this.endValue++
        if (this.endValue > this.allData.length - 1) {
          this.startValue = 0
          this.endValue = 9
        }
        this.echartInstance.setOption({
          dataZoom: {
            startValue: this.startValue,
            endValue: this.endValue
          }
        })
      }, 2000)
    }
  },
  created () {
    this.$socket.registerCallBack('rankData', this.getData)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'rankData',
      chartName: 'rank',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    this.screenAdapt()
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapt)
    clearInterval(this.timerId)
    this.$socket.unregisterCallBack('rankData')
  }
}
</script>

