<template>
  <div class="com-container">
    <div class="com-chart" ref="hot_ref"></div>
    <span class="iconfont left" :style="arrFontStyle" @click="toLeft">&#xe6eb;</span>
    <span class="iconfont right" :style="arrFontStyle" @click="toRight">&#xe6ee;</span>
    <span class="cat-name" :style="catNameFontSize">{{catName}}</span>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import themeUtils from '../utils/theme_utils'
export default {
  data () {
    return {
      echartInstance: null,
      allData: null,
      currentIndex: 0,
      catNameFontSize: {
        fontSize: 0
      },
      arrFontSize: 0
    }
  },
  computed: {
    arrFontStyle () {
      return {
        fontSize: this.arrFontSize,
        color: themeUtils.getThemeStyle(this.theme).color
      }
    },
    catName () {
      if (!this.allData) {
        return ''
      } else {
        return this.allData[this.currentIndex].name
      }
    },
    ...mapState(['theme'])
  },
  watch: {
    theme () {
      this.echartInstance.dispose()
      this.initChart()
      this.screenAdapt()
      this.updateChart()
    }
  },
  methods: {
    initChart () {
      this.echartInstance = this.$echarts.init(this.$refs.hot_ref, this.theme)
      const initOption = {
        title: {
          text: '|   热销商品销售金额占比统计',
          left: '1%',
          top: '5%'
        },
        legend: {
          top: '20%',
          icon: 'circle'
        },
        tooltip: {},
        series: [
          {
            type: 'pie',
            label: {
              show: false
            },
            emphasis: {
              label: {
                show: true
              },
              labelLine: {
                show: false
              }
            }
          }
        ]
      }
      this.echartInstance.setOption(initOption)
    },
    getData (res) {
      this.allData = res
      this.updateChart()
    },
    updateChart () {
      const legendArr = this.allData[this.currentIndex].children.map(item => {
        return item.name
      })
      const seriesData = this.allData[this.currentIndex].children.map(item => {
        return {
          name: item.name,
          value: item.value,
          children: item.children
        }
      })
      const updateOption = {
        tooltip: {
          formatter: arg => {
            const thirdCategory = arg.data.children
            let total = 0
            let str = ''
            thirdCategory.forEach(item => {
              total += item.value
            })
            thirdCategory.forEach(item => {
              str += `${item.name}：${parseInt(item.value / total * 100)}%<br />`
            })
            return str
          }
        },
        series: {
          data: seriesData
        },
        legend: {
          data: legendArr
        }
      }
      this.echartInstance.setOption(updateOption)
    },
    screenAdapt () {
      const titleFontSize = this.$refs.hot_ref.offsetWidth / 100 * 3.6
      const adaptOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize
          }
        },
        legend: {
          textStyle: {
            fontSize: titleFontSize / 2
          },
          itemWidth: titleFontSize / 2,
          itemHeight: titleFontSize / 2,
          itemGap: titleFontSize / 2
        },
        series: {
          center: ['50%', '60%'],
          radius: titleFontSize * 4
        }
      }
      this.arrFontSize = titleFontSize * 2 + 'px'
      this.catNameFontSize.fontSize = titleFontSize / 1.5 + 'px'
      this.echartInstance.setOption(adaptOption)
      this.echartInstance.resize()
    },
    toLeft () {
      this.currentIndex--
      if (this.currentIndex < 0) {
        this.currentIndex = this.allData.length - 1
      }
      this.updateChart()
    },
    toRight () {
      this.currentIndex++
      if (this.currentIndex > this.allData.length - 1) {
        this.currentIndex = 0
      }
      this.updateChart()
    }
  },
  created () {
    this.$socket.registerCallBack('hotData', this.getData)
  },
  mounted () {
    this.initChart()
    this.$socket.sendData({
      action: 'getData',
      socketType: 'hotData',
      chartName: 'hotproduct',
      value: ''
    })
    window.addEventListener('resize', this.screenAdapt)
    this.screenAdapt()
  },
  destroyed () {
    window.removeEventListener('resize', this.screenAdapt)
    this.$socket.unregisterCallBack('getData')
  }
}
</script>

<style lang="less" scoped>
  .com-container{
    position: relative;
  }
  .left,.right{
    position: absolute;
    top: 50%;
    cursor: pointer;
    color: #fff;
    user-select: none;
  }
  .left{
    left: 10%;
  }
  .right{
    right: 10%;
  }
  .cat-name{
    position: absolute;
    right: 5%;
    bottom: 5%;
    color: #fff;
  }
</style>
