<template>
  <div class="screen-container" :style="bgColor">
    <header class="screen-header" :style="color">
      <div class="header-left"></div>
      <div class="header-middle">
        <span>电商平台实时监控</span>
      </div>
      <div class="header-right">
        <div>
          <img :src="themeImg" @click="handleChangeTheme">
          <span>{{nowTime}}</span>
        </div>
      </div>
    </header>
    <div class="screen-body">
      <div class="screen-body-left">
        <div class="top" :class="[fullscreen.seller? 'fullscreen': '']">
          <seller ref="seller" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.seller? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('seller')" :style="color"></span>
          </div>
        </div>
        <div class="bottom" :class="[fullscreen.trend? 'fullscreen': '']">
          <trend ref="trend" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.trend? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('trend')" :style="color"></span>
          </div>
        </div>
      </div>
      <div class="screen-body-middle">
        <div class="top" :class="[fullscreen.map? 'fullscreen': '']">
          <Map ref="map" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.map? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('map')" :style="color"></span>
          </div>
        </div>
        <div class="bottom" :class="[fullscreen.rank? 'fullscreen': '']">
          <rank ref="rank" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.rank? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('rank')" :style="color"></span>
          </div>
        </div>
      </div>
      <div class="screen-body-right">
        <div class="top" :class="[fullscreen.stock? 'fullscreen': '']">
          <stock ref="stock" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.stock? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('stock')" :style="color"></span>
          </div>
        </div>
        <div class="bottom" :class="[fullscreen.hot? 'fullscreen': '']">
          <hot ref="hot" />
          <div class="icon-expand-wrap">
            <span class="iconfont" :class="[fullscreen.hot? 'icon-compress-alt': 'icon-expand-alt']" @click="sendFullScreenData('hot')" :style="color"></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import hot from '../components/hot'
import Map from '../components/map'
import rank from '../components/rank'
import seller from '../components/seller'
import stock from '../components/stock'
import trend from '../components/trend'
import themeUtils from '../utils/theme_utils'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      timerId: null,
      nowTime: '',
      fullscreen: {
        hot: false,
        map: false,
        rank: false,
        seller: false,
        stock: false,
        trend: false
      }
    }
  },
  computed: {
    themeImg () {
      return '/statics/img/' + themeUtils.getThemeStyle(this.theme).themeImg
    },
    bgColor () {
      return 'background-color:' + themeUtils.getThemeStyle(this.theme).backgroundColor
    },
    color () {
      return {
        color: themeUtils.getThemeStyle(this.theme).color,
        backgroundImage: 'url("/statics/img/' + themeUtils.getThemeStyle(this.theme).headerBgImg + '")'
      }
    },
    ...mapState(['theme'])
  },
  components: {
    hot,
    Map,
    rank,
    seller,
    stock,
    trend
  },
  methods: {
    startInterval () {
      if (this.timerId) {
        clearTimeout(this.timerId)
      }
      this.timerId = setTimeout(() => {
        this.getTime()
        this.startInterval()
      }, 1000)
    },
    getTime () {
      const date = new Date()
      const yy = date.getFullYear()
      const mm = numFormat(date.getMonth() + 1)
      const dd = numFormat(date.getDate())
      const hh = numFormat(date.getHours())
      const min = numFormat(date.getMinutes())
      const ss = numFormat(date.getSeconds())
      function numFormat (num) {
        if (num < 10) {
          return '0' + num
        }
        return num
      }
      this.nowTime = yy + '-' + mm + '-' + dd + ' ' + hh + ':' + min + ':' + ss
    },
    sendFullScreenData (type) {
      const isFullScreen = !this.fullscreen[type]
      this.$socket.sendData({
        action: 'fullScreen',
        socketType: 'fullScreen',
        chartName: type,
        value: isFullScreen
      })
    },
    fullScreenToggle (data) {
      const type = data.chartName
      const value = data.value
      this.fullscreen[type] = value
      this.$nextTick(() => {
        this.$refs[type].screenAdapt()
      })
    },
    handleChangeTheme () {
      const themeValue = !this.$store.theme
      this.$socket.sendData({
        action: 'changeTheme',
        socketType: 'changeTheme',
        value: themeValue
      })
    },
    changeTheme (data) {
      this.$store.commit('changeTheme')
    }
  },
  created () {
    this.$socket.registerCallBack('fullScreen', this.fullScreenToggle)
    this.$socket.registerCallBack('changeTheme', this.changeTheme)
  },
  mounted () {
    this.startInterval()
    this.getTime()
  },
  destroyed () {
    clearTimeout(this.timerId)
    this.$socket.unregisterCallBack('fullScreen')
  }
}
</script>

<style lang="less" scoped>
  .fullscreen{
    position: fixed!important;
    top: 0!important;
    left: 0!important;
    width: 100%!important;
    height: 100%!important;
    margin: 0!important;
    z-index: 100;
  }
  .screen-container{
    width: 100%;
    height: 100%;
    padding-bottom: 10px;
  }
  .screen-header{
    width: 100%;
    height: 72px;
    margin-bottom: 10px;
    background-position-x: center;
    display: flex;
    display: -webkit-flex;
    div{
      flex: 1;
      line-height: 72px;
    }
    .header-middle{
      text-align: center;
      font-size: 30px;
    }
    .header-right{
      text-align: right;
      font-size: 20px;
      img{
        margin-right: 20px;
        cursor: pointer;
      }
      div{
        padding-right: 40px;
      }
    }
  }
  .screen-body{
    height: calc(100vh - 82px);
    display: flex;
    display: -webkit-flex;
    div{
      height: 100%;
    }
    .top,.bottom{
      position: relative;
    }
    .icon-expand-wrap{
      position: absolute;
      right: 4%;
      top: 4%;
      .iconfont{
        color: #fff;
        cursor: pointer;
      }
      .icon-compress-alt{
        font-size: 2rem;
      }
    }
    .screen-body-left{
      width: 30%;
      display: flex;
      display: -webkit-flex;
      flex-direction: column;
      .top{
        flex: 3;
        margin-bottom: 10px;
      }
      .bottom{
        flex: 2;
      }
    }
    .screen-body-middle{
      width: 40%;
      display: flex;
      display: -webkit-flex;
      flex-direction: column;
      margin: 0 10px 0;
      .top{
        flex: 5;
        margin-bottom: 10px;
      }
      .bottom{
        flex: 2;
      }
    }
    .screen-body-right{
      width: 30%;
      display: flex;
      display: -webkit-flex;
      flex-direction: column;
      .top{
        flex: 3;
        margin-bottom: 10px;
      }
      .bottom{
        flex: 2;
      }
    }
  }
</style>
